import React from 'react'

const Header = ({ category,tasks,icon }) => {
    return (
        <div>
            <div className='header'>
            
            <h3><span>{icon}</span> <span>{category} tasks</span></h3>
            <div className='number'>{tasks.filter(task => task.status === category).length}</div>
          </div>
        </div>
    )
}

export default Header
