import { useState } from 'react'
import { useEffect } from 'react'
import { HiLightBulb } from 'react-icons/hi'
import { BsFillGearFill } from'react-icons/bs'
import { BiCheckCircle } from'react-icons/bi'
import { AiFillCheckCircle } from'react-icons/ai'


import Tabs from "./components/Tabs";
import Tab from "./components/Tab";
import OpenTasks from './components/OpenTasks'
import RunningTasks from './components/RunningTasks'
import FinishedTasks from './components/FinishedTasks'
import Header from './components/Header'

function App() {
  const [tasks, setTasks] = useState([])

  useEffect(() => {
    const storedTasks = JSON.parse(localStorage.getItem('storedTasks'));
    if (storedTasks) {
      setTasks(storedTasks);
    }
  }, []);
  useEffect(() => {
    localStorage.setItem('storedTasks', JSON.stringify(tasks));
  }, [tasks]);

  const addTask = (text, details, time) => {
    console.log('appAddTask', text)
    const id = Math.floor(Math.random() * 5000)
    const newTask = { id, text, details, time, status: 'open' }
    setTasks(tasks => [...tasks, newTask])
  }
  const deleteTask = (id) => {
    console.log('deleteTask', id)
    setTasks(tasks.filter(task => task.id !== id))
  }
  const selectTask = (id) => {
    console.log('selectTask', id)
    setTasks(tasks.map(task => {
      if (task.id === id)
        task.status = 'running';
      return task;
    }))
  }
  const stoppedTask = (id) => {
    console.log('stoppedTask', id)
    setTasks(tasks => tasks.map(task => {
      if (task.id === id)
        task.status = 'open';
      return task;
    }))
  }
  const finishedTask = (id) => {
    console.log('finishedTask', id)
    setTasks(tasks => tasks.map(task => {
      if (task.id === id)
        task.status = 'finished';
      return task;
    }))
  }

  return (
    <div>

      <div className="app">

        <div className='category openTasks'>
          <Header tasks={tasks} category='open' icon={<HiLightBulb size={18}/>} />
          <OpenTasks tasks={tasks} addTask={addTask} selectTask={selectTask} deleteTask={deleteTask} />
        </div>
        <div className='category runningTasks'>
          <Header tasks={tasks} category='running' icon={<BsFillGearFill size={16}/>}/>
          <RunningTasks tasks={tasks} finishedTask={finishedTask} stoppedTask={stoppedTask} />
        </div>
        <div className='category finishedTasks'>
          <Header tasks={tasks} category='finished' icon={<AiFillCheckCircle size={16}/>}/>
          <FinishedTasks tasks={tasks} deleteTask={deleteTask} />
        </div>

      </div>
      <div className='tabs'>
        <Tabs>
          <Tab value="1" header={<div className='tab-header'><span><HiLightBulb /> Open</span><span>{tasks.filter(task => task.status === 'open').length}</span></div>}>
            <OpenTasks tasks={tasks} addTask={addTask} selectTask={selectTask} deleteTask={deleteTask} />
          </Tab>
          <Tab value="2" header={<div className='tab-header'><span><BsFillGearFill/> Running</span><span>{tasks.filter(task => task.status === 'running').length}</span></div>}>
            <RunningTasks tasks={tasks} finishedTask={finishedTask} stoppedTask={stoppedTask} />
          </Tab>
          <Tab value="3" header={<div className='tab-header'><span><AiFillCheckCircle/> Finished</span><span>{tasks.filter(task => task.status === 'finished').length}</span></div>}>
            <FinishedTasks tasks={tasks} deleteTask={deleteTask} />
          </Tab>
        </Tabs>
      </div>
    </div>


  );
}

export default App;
